/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  color: #000;
}

p {
  line-height: 1.7;
  margin: 0;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
  font-family: "Poppins", sans-serif;
  color: #00252e;
  line-height: 1.7;
}

h1 {
  font-size: 2.9rem;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.3rem;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3rem;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

@keyframes updown {
  0% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(2rem);
  }
  100% {
    transform: translateY(0rem);
  }
}
.header {
  padding: 3rem 0 11rem 0;
}
.header__barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10rem;
}
.header__logo {
  width: 15rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 20rem;
  }
}
.header__enlace {
  font-size: 1.4rem;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  padding: 1.5rem 3.5rem;
  text-align: center;
  color: #ff52bf;
  text-decoration: none;
  border: none;
  border-radius: 5rem;
  background-color: #ff52bf;
  transition: all 0.3s ease;
  background-color: transparent;
  font-weight: normal;
  border: 1px solid #ff52bf;
  margin-top: 0;
  padding: 1rem 2.5rem;
}
@media (min-width: 768px) {
  .header__enlace {
    padding: 1.7rem 7.5rem;
  }
}
.header__enlace:hover {
  cursor: pointer;
  color: #fff;
  background-color: #ff8fd8;
}
@media (min-width: 768px) {
  .header__enlace {
    padding: 1rem 2.5rem;
  }
}
.header__enlace:hover {
  border: 1px solid #ff8fd8;
  background-color: transparent;
  color: #ff8fd8;
}
.header__contenido {
  text-align: center;
}
@media (min-width: 768px) {
  .header__contenido {
    width: 80%;
    margin: 0 auto;
  }
}
.header__texto {
  max-width: 49rem;
  margin: 0 auto 2rem auto;
}
.header__link-c {
  font-size: 1.4rem;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  padding: 1.5rem 3.5rem;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 5rem;
  background-color: #ff52bf;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .header__link-c {
    padding: 1.7rem 7.5rem;
  }
}
.header__link-c:hover {
  cursor: pointer;
  color: #fff;
  background-color: #ff8fd8;
}
.header__imagen {
  margin-top: 8rem;
  transition-property: transform;
  transition-duration: 0.3s;
  animation-name: updown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@media (min-width: 576px) {
  .header__imagen {
    width: 80%;
    margin: 8rem auto 0 auto;
  }
}
@media (min-width: 768px) {
  .header__imagen {
    width: 90%;
  }
}

.stats__contenido {
  display: grid;
  gap: 10rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .stats__contenido {
    width: 80%;
    margin: 0 auto;
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .stats__contenido {
    width: 60%;
  }
}

.stat {
  text-align: center;
  margin: auto;
}
.stat__cantidad {
  font-size: 6rem;
  color: #00252e;
  font-weight: 700;
}
.stat__nombre {
  color: gray;
}
.stat__nombre span {
  display: block;
}
@media (min-width: 1024px) {
  .stat__nombre span {
    display: inline-block;
  }
}

.features {
  margin-top: 8rem;
  display: grid;
  gap: 12rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .features {
    gap: 10rem;
  }
}

.feature {
  position: relative;
}
@media (min-width: 768px) {
  .feature:nth-child(even) .feature__contenido {
    flex-direction: row-reverse;
  }
}
.feature__bg {
  background-color: #f5faff;
}
.feature__ola {
  width: 100%;
}
@media (min-width: 768px) {
  .feature__ola {
    display: none;
  }
}
.feature__ola-d {
  display: none;
}
@media (min-width: 768px) {
  .feature__ola-d {
    display: block;
    width: 100%;
  }
}
.feature__contenido {
  padding: 4rem 0;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 4rem;
}
@media (min-width: 768px) {
  .feature__contenido {
    flex-direction: row;
  }
}
.feature__info {
  text-align: center;
}
@media (min-width: 768px) {
  .feature__info {
    text-align: left;
    max-width: 45rem;
  }
}
@media (min-width: 1024px) {
  .feature__info {
    max-width: 52rem;
  }
}
.feature__imagen {
  transition: all 0.3s;
  margin-bottom: 3rem;
  animation-name: updown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@media (min-width: 576px) {
  .feature__imagen {
    width: 70%;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .feature__imagen {
    width: 40%;
  }
}

.comunidad {
  padding: 15rem 0;
  text-align: center;
}
.comunidad__enlace {
  font-size: 1.4rem;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  padding: 1.5rem 3.5rem;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 5rem;
  background-color: #ff52bf;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .comunidad__enlace {
    padding: 1.7rem 7.5rem;
  }
}
.comunidad__enlace:hover {
  cursor: pointer;
  color: #fff;
  background-color: #ff8fd8;
}

.footer__ola {
  width: 100%;
  margin-bottom: -0.5rem;
}
@media (min-width: 768px) {
  .footer__ola {
    display: none;
  }
}
.footer__ola-d {
  display: none;
}
@media (min-width: 768px) {
  .footer__ola-d {
    display: block;
    width: 100%;
    margin-bottom: -0.5rem;
  }
}
.footer__bg {
  background-color: #00252e;
}
.footer__contenido {
  padding: 4rem 0;
  display: flex;
  flex-direction: column-reverse;
  gap: 5rem;
}
@media (min-width: 768px) {
  .footer__contenido {
    width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .footer__contenido {
    width: 100%;
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__logo {
  width: 10rem;
  margin-bottom: 2rem;
  display: inline-block;
}
.footer__logo svg path {
  fill: #fff;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 20rem;
  }
}
.footer__contacto {
  color: #fff;
}
@media (min-width: 1024px) {
  .footer__contacto {
    max-width: 31rem;
  }
}
.footer__lorem {
  margin-bottom: 5rem;
}
.footer__ul {
  padding-left: 3rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.footer__li {
  position: relative;
}
.footer__li:first-of-type::before {
  content: "";
  background-image: url(../img/icon-phone.svg);
  background-repeat: no-repeat;
  background-size: 1.8rem;
  width: 1.8rem;
  height: 1.8rem;
  position: absolute;
  top: -0.2rem;
  left: -2.8rem;
}
.footer__li:last-of-type::before {
  content: "";
  background-image: url(../img/icon-email.svg);
  background-repeat: no-repeat;
  background-size: 1.8rem;
  width: 1.8rem;
  height: 1.8rem;
  position: absolute;
  top: 0.3rem;
  left: -2.8rem;
}
.footer__form {
  color: #fff;
}
.footer__heading {
  color: #fff;
}
.footer__texto {
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .footer__texto {
    max-width: 38rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .form {
    flex-direction: row;
    gap: 3rem;
  }
}
.form__input-c {
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.form__input {
  padding: 1.4rem;
  border-radius: 0.5rem;
  border: none;
}
.form__input.error {
  border: 1px solid #ff4242;
}
.form__error {
  display: none;
  font-size: 1.4rem;
  color: #ff4242;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.3s;
}
.form__error.activo {
  display: block;
  opacity: 1;
}
.form__submit {
  font-size: 1.4rem;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  padding: 1.5rem 3.5rem;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 5rem;
  background-color: #ff52bf;
  transition: all 0.3s ease;
  border-radius: 0.5rem;
  padding: 1.4rem;
  margin: 0;
  width: 30%;
}
@media (min-width: 768px) {
  .form__submit {
    padding: 1.7rem 7.5rem;
  }
}
.form__submit:hover {
  cursor: pointer;
  color: #fff;
  background-color: #ff8fd8;
}
@media (min-width: 768px) {
  .form__submit {
    padding: 1.4rem;
  }
}

.iconos {
  margin-top: 5rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.iconos__a {
  color: #fff;
  font-size: 2.5rem;
}
.iconos__a:hover {
  color: lightseagreen;
}

.autor {
  color: #fff;
  text-align: center;
}
.autor a {
  color: #ff52bf;
}
.autor a:hover {
  text-decoration: underline;
}